<template>
  <div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <input v-model="total" class="weui-input weui-input_disabled" type="number" pattern="[0-9\.]*" placeholder="">
        </div>
        <div class="weui-cell__ft weui-cell__ft_rg">
          <div class="weui-radio-group">
            <label class="weui-radio-button" for="r1">
              <input id="r1" v-model="fmultiple" name="fmultiple" type="radio" value="10000">
              <span></span>
            </label>
            <label class="weui-radio-button" for="r2">
              <input id="r2" v-model="fmultiple" name="fmultiple" type="radio" value="1">
              <span></span>
            </label>
          </div>
        </div>
      </div>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd"><label for="" class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <input v-model="sf" class="weui-input weui-input_disabled" type="number" pattern="[0-9\.]*" placeholder="">
        </div>
        <div class="weui-cell__ft weui-cell__ft_rg">
          <div class="weui-radio-group">
            <label class="weui-radio-button" for="r3">
              <input id="r3" v-model="smultiple" name="smultiple" type="radio" value="10000">
              <span></span>
            </label>
            <label class="weui-radio-button" for="r4">
              <input id="r4" v-model="smultiple" name="smultiple" type="radio" value="1">
              <span></span>
            </label>
          </div>
        </div>
      </div>

      <div class="weui-cell  weui-cell_select weui-cell_select-after">
        <div class="weui-cell__hd"><label for="s1" class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <select class="weui-select" v-model="period" id="s1">
            <option v-for="n in 30" :value="n * 12">{{n}} - {{n*12}}</option>
          </select>
        </div>
      </div>
      <div class="weui-cell weui-cell_vcode">
        <div class="weui-cell__hd">
          <label class="weui-label" id="i1"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" id="i1" v-model="rate" type="number" placeholder="">
        </div>
        <div class="weui-cell__ft">
          <span class="weui-vcode-btn percent">%</span>
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label for="i2" class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <input v-model="fee" id="i2" class="weui-input weui-input_disabled" type="number" pattern="[0-9\.]*" placeholder="">
        </div>
        <div class="weui-cell__ft weui-cell__ft_rg">
          <div class="weui-radio-group">
            <label class="weui-radio-button" for="r9">
              <input id="r9" v-model="femultiple" name="femultiple" type="radio" value="10000">
              <span></span>
            </label>
            <label class="weui-radio-button" for="r7">
              <input id="r7" v-model="femultiple" name="femultiple" type="radio" value="1">
              <span></span>
            </label>
          </div>
        </div>
      </div>

      <div class="weui-cell  weui-cell_select weui-cell_select-after">
        <div class="weui-cell__hd"><label for="" class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <select class="weui-select" v-model="type">
            <option value="1"></option>
            <option value="2"></option>
          </select>
        </div>
      </div>
    </div>


    <div class="weui-cells__title"></div>
    <div class="weui-cells">
      <div class="weui-cell weui-cell_vcode">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" v-model="yrate" type="number" placeholder="">
        </div>
        <div class="weui-cell__ft">
          <span class="weui-vcode-btn percent">%</span>
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label for="i3" class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <input id="i3" v-model="fz" class="weui-input weui-input_disabled" type="number" pattern="[0-9\.]*" placeholder="">
        </div>
      </div>
      <div class="weui-cell  weui-cell_select weui-cell_select-after">
        <div class="weui-cell__hd"><label for="s3" class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <select id="s3" class="weui-select" v-model="iperiod">
            <option v-for="n in 10" :value="n * 12">{{n}}</option>
          </select>
        </div>
      </div>
    </div>
    <div class="weui-btn-area"> <a @click="doit"class="weui-btn weui-btn_primary"></a> </div>

    <div v-show="result">
      <div class="weui-form-preview">
        <div class="weui-form-preview__hd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label"></label>
            <em class="weui-form-preview__value">{{result}}%</em>
          </div>
        </div>
        <div class="weui-form-preview__bd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label"></label>
            <span class="weui-form-preview__value">{{interest}}</span>
          </div>
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label"></label>
            <span class="weui-form-preview__value">{{earnings}}</span>
          </div>
        </div>
      </div>
      <br>
      <div class="weui-form-preview">
        <div class="weui-form-preview__hd">
          <div class="weui-form-preview__item">
            <label class="weui-form-preview__label"></label>
          </div>
        </div>
        <div class="weui-form-preview__bd">

          <div class="weui-form-preview__item">
            <p class="weui-form-preview__desc indent"><b>{{iperiod/12}}</b><b>{{yrate}}%</b></p>
            <ul>
              <li> &gt; <b>{{result}}%</b></li>
              <li> &lt; <b>{{result}}%</b></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import weui from 'weui'
  import excel from 'excel'
  export default {
    data () {
      return {
        appData: {
          title: ' or ',
          description: '100'
        },
        total: '',
        femultiple: '10000',
        rate: '4.9', // 
        fee: '',
        fmultiple: '10000',
        sf: '',
        smultiple: '10000',
        dktype: 'sy',
        period: '240',
        type: '1',
        yrate: '10',
        fz: '',
        iperiod: '60',
        // 
        result: '',
        interest: '',
        earnings: ''
      }
    },
    created () {
      this.$emit('sendTitleDesc', this.appData)
      document.title = this.appData.title
    },
    methods: {
      doit () {
        // todo
        // 1. ===
        //
        var done = true
        var t = this;
        ['total', 'rate', 'sf'].forEach(function (id) {
          if (!t[id]) {
            done && weui.topTips('', {
              duration: 3000,
              className: 'custom-classname'
            })
            done = false
          }
        })
        var fz = this.fz | 0
        var interest = 0
        var total = this.total * this.fmultiple
        var sf = this.sf * this.smultiple
        var dai = total - sf
        var fee = this.fee * this.femultiple
        var rate = this.rate / 12 / 100
        var period = this.period | 0
        var iperiod = this.iperiod | 0
        var temp = 0
        var i = 1
        if (dai !== 0) {
          if (this.type === '1') {
            // 
            for (i = 1; i <= iperiod; i++) {
              temp = parseFloat(excel.IPMT(rate, i, period, -dai).toFixed(2))
              interest += temp
            }
            interest = interest.toFixed(2)
          } else {
            // 
            // 
            var b = dai / period
            for (i = 0; i < iperiod; i++) {
              // 
              temp = -Math.floor(-(dai - b * i) * rate)
              interest += temp
            }
            interest = interest.toFixed(2)
          }
        } else {
          // 
        }

        interest = parseFloat(interest)
        //  + - 
        var earnings = parseFloat((total * this.yrate / 100 + fz - interest).toFixed(2))
        //  =  + + -  -  - 
        this.earnings = earnings
        // 
        var cost = (sf + fee)
        if (iperiod === 12) {
          // 
          this.result = (earnings / cost * 100).toFixed(2)
        } else {
          // 
          var moneys = [-cost, cost + earnings]
          var dates = [new Date(2000, 0, 1), new Date(2000 + iperiod / 12, 1, 1)]
          this.result = (excel.XIRR(moneys, dates) * 100).toFixed(2)
        }

        this.interest = interest
      }
    }
  }
</script>
<style lang="scss" scoped>
.weui-form-preview__item{
  li{
      text-align: left;
      list-style-position: inside;
      list-style-type: decimal;
    }
  .weui-form-preview__desc{
    text-align: left;
    b{
      color: #F43530
    }

  }
}
</style>



